@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
.container {
  position: relative;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

.introduce-container {
  width: 100%;
}

.title-img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 50px auto;
}

.restaurant-contact {
  margin-bottom: 20px;
  text-align: center;
}

.restaurant-contact > p > i {
  margin-right: 5px;
}

.restaurant-name {
  font-size: 24px;
  line-height: 1.1em;
}

.restaurant-comment {
  margin-bottom: 20px;
  color: rgb(102, 102, 102);
}

address > p {
  line-height: 1.3em;
}

.insta {
  transition: color 200ms;
}

.insta:hover {
  color: #8fbc8f;
}

.hours {
  font-size: 15px;
  font-weight: 700;
  text-align: center;
}

.submit-form {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto 20px auto;
}

.user-name,
.user-commnet {
  padding: 5px;
  border: 1px solid #ededed;
  font-size: 13px;
}

.user-name {
  width: 100px;
  height: 35px;
  border-radius: 5px;
}

.user-commnet {
  width: 500px;
  height: 100px;
  margin-left: 30px;
  margin-bottom: 20px;
  border-radius: 7px;
  resize: none;
}

.submit-button {
  width: 100px;
  height: 40px;
  margin: 0 30px;
  border-radius: 5px;
  background-color: #ddd2be;
  transition: background-color 200ms;
}

.submit-button:hover {
  color: #808080;
  background-color: #ebe6de;
}

.review-list {
  width: 100%;
  height: 360px;
  margin: 0 auto 20px auto;
  overflow-y: scroll;
}

.review-list::-webkit-scrollbar {
  width: 12px; /* Chrome, Safari, Opera*/
}

.review-list::-webkit-scrollbar-thumb {
  border: #e0e0e0 solid 1px;
  border-radius: 6px;
  background: #ffffff;
}

.review-list::-webkit-scrollbar-track {
  border-radius: 6px;
  background: #e0e0e0;
}

.comment-box {
  display: flex;
  width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f7f7f7;
  border-radius: 10px;
}

.avatar {
  position: relative;
  top: 5px;
  width: 36px;
  height: 36px;
}

.comment {
  width: 85%;
  margin-left: 20px;
}

.comment-user-name {
  font-size: 13px;
}

.time {
  color: #808080;
  font-size: 11px;
}

.text {
  margin-top: 5px;
  font-size: 13px;
  word-break: keep-all;
}

.like {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.like-button {
  font-size: 18px;
  color: #e0e5e7;
}

.like-count {
  font-size: 11px;
}

.fade-in-box {
  animation: fadein 0.3s;
  -moz-animation: fadein 0.3s; /* Firefox */
  -webkit-animation: fadein 0.3s; /* Safari and Chrome */
  -o-animation: fadein 0.3s; /* Opera */
}

.showMore {
  display: block;
  width: 100px;
  height: 40px;
  background-color: thistle;
  margin: 20px auto;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  /* Safari and Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 768px) {
  .title-img {
    height: auto;
  }

  .review-list {
    width: 95%;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }

  .review-list::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
  }

  .submit-form {
    width: 95%;
    justify-content: space-around;
  }

  .user-name {
    width: 20%;
  }

  .user-commnet {
    width: 75%;
    margin-left: 0;
  }

  .review-list {
    width: 95%;
  }

  .comment-box {
    width: 100%;
  }
}

@media screen and (max-width: 414px) {
  .restaurant-name {
    font-size: 20px;
  }

  .restaurant-contact {
    font-size: 12px;
  }

  .hours {
    font-size: 14px;
  }
}

@media screen and (max-width: 320px) {
  .info {
    margin: 30px 0;
  }

  .restaurant-name {
    font-size: 17px;
  }

  .restaurant-comment {
    font-size: 11px;
  }

  .hours {
    font-size: 13px;
  }

  .user-commnet {
    height: 80px;
  }

  .submit-button {
    width: 80px;
    height: 35px;
  }
}

* {
  margin: 0;
  font-family: "Noto Sans KR", sans-serif;
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

html {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 13px;
}

body {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 13px;
}

h1 {
  margin: 0;
}

a,
a:visited {
  color: inherit;
  text-decoration: none;
}

button,
input,
select,
textarea {
  background-color: transparent;
  border: 0;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none;
}

button {
  cursor: pointer;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

label {
  margin-right: 10px;
  font-size: 14px;
  line-height: 1.8em;
}

.wrapper {
  width: 100%;
}

.reservation-title {
  margin: 50px 0;
  font-size: 32px;
  text-align: center;
}

.reservation-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 700px;
  margin: 0 auto;
}

.reservation-form > div:not(:last-of-type) {
  margin-right: 30px;
}

.reservation-name {
  width: 100px;
  border-bottom: 1px solid #d3d3d3;
}

.reservation-name,
.reservation-time,
.visit-people {
  height: 1.8em;
  font-size: 14px;
}

.reservation-time,
.visit-people {
  width: 80px;
  border: 1px solid #d3d3d3;
}

.reservation-submit-button {
  width: 100px;
  height: 40px;
  margin: 40px 100px 0 100px;
  border-radius: 5px;
  font-size: 14px;
  background-color: #ddd2be;
  transition: background-color 200ms;
}

.reservation-submit-button:hover {
  color: #808080;
  background-color: #ebe6de;
}

.react-calendar {
  width: 700px;
  height: 470px;
  /* max-width: 100%; */
  margin: 0 auto;
  background: white;
  /* border: 1px solid #a0a096; */
  line-height: 1.125em;
  margin-bottom: 50px;
}
.react-calendar--doubleView {
  width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
  text-decoration: none;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  display: flex;
  height: 70px;
  margin-bottom: 1em;
  border-bottom: 1px solid #d3d3d3;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
  font-weight: 700;
}
.react-calendar__navigation__label {
  font-size: 18px;
}

.react-calendar__navigation__next-button,
.react-calendar__navigation__prev-button {
  font-size: 22px;
}
/* .react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #e6e6e6;
} */

.react-calendar__navigation button[disabled] {
  color: #b5bcc0;
}
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 13px;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  font-weight: bold;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}
.react-calendar__month-view__days__day--weekend {
  color: #d10000;
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: #757575;
  padding: 20px;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  display: block;
  height: 60px;
  max-width: 100%;
  text-align: center;
  /* padding: 0.75em 0.5em; */
  background: none;
  font-size: 13px;
}
.react-calendar__tile:disabled {
  color: #b5bcc0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e6;
}
/* .react-calendar__tile--now {
  background: rgb(207, 229, 238);
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: rgb(216, 235, 243);
} */
.react-calendar__tile--hasActive {
  background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #a9d4ff;
}
.react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #1087ff;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

